import ApiObject from "./Interfaces";
interface CarDataUi {
  data: ApiObject;
  mainIsCreated: HTMLElement | null;
  handleBtnClickIsActive: boolean;
}

type htmlElem = HTMLElement | null | HTMLButtonElement;

export default class Ui implements CarDataUi {
  data: ApiObject;
  mainIsCreated: htmlElem;
  TransmissionIcon: string;
  EngineIcon: string;
  cylinderIcon: string;
  literIcon: string;
  turboIcon: string;
  handleBtnClickIsActive: boolean;

  constructor(data: ApiObject) {
    this.TransmissionIcon =
      '<img src="data:image/svg+xml;utf8;base64," />';
    this.EngineIcon =
      '<img src="data:image/svg+xml;base64," />';

    this.cylinderIcon = `<img src="data:image/png;base64," />`;

    this.literIcon = `<img src="data:image/svg+xml;base64," />`;
    this.turboIcon = `<img src="data:image/svg+xml;base64," />`;
    this.handleBtnClickIsActive = false;
    (this.data = data), (this.mainIsCreated = document.querySelector("main"));
    if (this.mainIsCreated != null && this.mainIsCreated != undefined) {
      this.updateData();
    } else {
      this.printData();
    }
  }
  createElem = (elem: string): HTMLElement => document.createElement(`${elem}`);
  handleBtnClick = (ContainerDetailsRaport: htmlElem) => {
    if (ContainerDetailsRaport) {
      !this.handleBtnClickIsActive
        ? ContainerDetailsRaport.setAttribute("style", `display:flex`)
        : ContainerDetailsRaport.setAttribute("style", `display:none`);
      this.handleBtnClickIsActive = !this.handleBtnClickIsActive;
    }
  };
  setListDataElem = (): string => {
    const {
      make,
      manufacturer,
      model,
      transmission,
      trim,
      year,
      vin,
    } = this.data;

    return `<ul>
    <li>${vin}</li>
      <li>${make}</li>
      <li><i class='fas fa-industry'></i>${manufacturer}</li>
      <li><i class="fas fa-car"></i>${model}</li>
      <li>${this.TransmissionIcon} ${transmission}</li>
      <li><i class="fas fa-code-branch"></i>equipment ${trim}</li>
      <li><i class="fas fa-calendar-alt"></i>${year}</li>
      </ul>`;
  };
  printData = () => {
    // console.log(this.data);
    const { make, model, img, engine } = this.data;

    const aside: htmlElem = document.querySelector("aside");
    const main: htmlElem = this.createElem("main");

    const headerRaport: htmlElem = this.createElem("div");
    const mainRaport: htmlElem = this.createElem("div");
    const infoRaport: htmlElem = this.createElem("div");
    const imgRaport: htmlElem = this.createElem("div");
    const detailsRaport: htmlElem = this.createElem("div");
    const ContainerDetailsRaport: htmlElem = this.createElem("div");
    const nameCarHeader: htmlElem = this.createElem("h3");
    const infoRaportWrapper: htmlElem = this.createElem("div");
    const detailsBtn: htmlElem = this.createElem("button");

    const vinLocationIMG: htmlElem = this.createElem("img");

    headerRaport.className = "headerRaport";
    mainRaport.className = "mainRaport";
    infoRaport.className = "infoRaport";
    imgRaport.className = "imgRaport";
    ContainerDetailsRaport.className = "ContainerDetailsRaport";
    nameCarHeader.className = "NameCarHeader";
    infoRaportWrapper.className = "infoRaportWrapper";
    detailsRaport.className = "detailsRaport";
    vinLocationIMG.className = "vinImg";
    detailsBtn.className = "detailsBtn";
    detailsBtn.innerHTML = `Check <i class="fas fa-check"></i>`;

    const imgSrc: string = `../IMG/vinLocation.png`;

    nameCarHeader.innerText = `${make} ${model}`;
    infoRaportWrapper.innerHTML = this.setListDataElem();

    if (aside) {
      headerRaport.innerHTML = `<p>TWÓJ RAPORT POJAZDU</p><i class='fas fa-info-circle'></i>`;

      aside.before(main);
      main.appendChild(headerRaport);
      headerRaport.after(mainRaport);
      mainRaport.appendChild(infoRaport);
      infoRaport.after(imgRaport);
      mainRaport.after(detailsRaport);

      infoRaport.appendChild(nameCarHeader);
      nameCarHeader.after(infoRaportWrapper);

      detailsRaport.appendChild(detailsBtn);
      imgRaport.appendChild(vinLocationIMG);
      main.after(ContainerDetailsRaport);

      //details -Engine
      const engineDesc = this.createElem("div");
      engineDesc.className = "engineDesc";

      engineDesc.innerHTML = `<div><p>ENGINE</p>${this.EngineIcon}</div>`;

      const engineDetails = this.createElem("div");
      engineDetails.className = "engineDetails";

      const carRaportPDF = this.createElem("div");
      carRaportPDF.className = "carRaportPDF";
      carRaportPDF.innerHTML =
        '<div><p>Generate <span>PDF</span> Rapport</p><i class="fas fa-file-pdf"></i></div>';
      const engineSpec = this.createElem("div");
      const engineIcons = this.createElem("div");
      engineIcons.innerHTML = `${this.literIcon} ${this.cylinderIcon} ${this.turboIcon}`;
      engineSpec.className = "engineSpec";
      engineIcons.className = "engineIcons";
      engineDetails.appendChild(engineSpec);
      engineSpec.appendChild(engineIcons);
      const engineDataBar = this.createElem("div");
      engineDataBar.className = "engineDataBar";
      engineIcons.after(engineDataBar);
      engineDataBar.innerHTML = `<p>${engine}</p>`;

      ContainerDetailsRaport.appendChild(engineDesc);

      engineDesc.after(engineDetails);
      engineDetails.after(carRaportPDF);

      detailsBtn.addEventListener("click", () =>
        this.handleBtnClick(ContainerDetailsRaport)
      );
      const imgLocation: HTMLImageElement | null = document.querySelector(
        ".vinImg"
      );
      if (imgLocation) {
        imgLocation.setAttribute("src", img);
      }
    }
  };
  updateData = () => {
    const { make, model, img, engine } = this.data;
    let ContainerDetailsRaport: htmlElem = document.querySelector(
      ".ContainerDetailsRaport"
    );
    //this.handleBtnClickIsActive = true;

    this.handleBtnClickIsActive = true;
    this.handleBtnClick(ContainerDetailsRaport);

    this.handleBtnClickIsActive = false;

    let infoRaportWrapper: htmlElem = document.querySelector(
      ".infoRaportWrapper"
    );
    let nameCarHeader: htmlElem = document.querySelector(".NameCarHeader");

    let engineDataBar: htmlElem = document.querySelector(".engineDataBar");
    if (infoRaportWrapper && nameCarHeader && engineDataBar) {
      nameCarHeader.innerText = `${make} ${model}`;
      infoRaportWrapper.innerHTML = this.setListDataElem();
      engineDataBar.innerHTML = `<p>${engine}</p>`;
    }
    const imgLocation: HTMLImageElement | null = document.querySelector(
      ".vinImg"
    );
    imgLocation ? imgLocation.setAttribute("src", img) : null;
  };
}
